body {
    background-color: #f8efd6;
}

.input-ctn {
    width: 30%;
    margin: 0 auto;
    margin-top: 70px;
}
.pnt {
    margin-bottom: 40px;
}
.Input {
    color:rgb(28, 28, 28);
    width: 100%;
    height:40px;
    background: transparent;
    border: none;
    padding: 21px 1px;
    border-radius: 0px;
    border-bottom: 1px solid rgb(50, 56, 66);
    font-size: 14px;
    font-weight: 500;
}
.Input-d{
    color:rgb(222, 222, 222);
    width: 100%;
    height:40px;
    background: transparent;
    border: none;
    padding: 21px 1px;
    border-radius: 0px;
    border-bottom: 1px solid rgb(44, 93, 255);
    font-size: 14px;
    font-weight: 500;
}
.Input-d:focus{
    outline-width: 0;
    background: transparent;
    color: rgb(235, 235, 235);
}

.Input:focus{
outline-width: 0;
   background: transparent;
   color: rgb(35, 35, 35);
}
.Label {
    color:rgb(74, 74, 74);
    font-size: 14px;
    font-weight: 500;
}

.text-title {
    color: #F45F38;
    font-weight: 500;
    font-size: 22px;
    margin : 22px 0;
}
.title-desc {
    font-weight: 600;
    font-size: 14px;
    text-align: center;
    color: #f5562e;
    margin: 50px 0;
    letter-spacing: .5px;
}
.Radio {
    color:rgb(61, 61, 61);
    font-size: 14px;
    font-weight: 500;
    margin-left: 12px;
}

.Input-S {
    width: 70px;
    color:rgb(28, 28, 28);
    height:20px;
    background: transparent;
    border: 1px rgb(130, 130, 130) solid;
    padding: 17px 15px;
    border-radius: 0px;
    font-size: 14px;
    font-weight: 500;
    margin-right: 10px;
    margin-top: 22px;
    border-radius: 3px;
}
.Input-Sel {
    font-size: 14px;
    font-weight: 500;
    color: rgb(49, 49, 49);
    border-radius: 6px;
}
.Input-Sel:focus {
    outline-width: 0;
}
.Label-S{
    color:rgb(74, 74, 74);
    font-size: 14px;
    font-weight: 500;
    width: 100%;
}
.btn-ctn {
    width: 100%;
    height: fit-content;
    margin: 62px 0;
}
.btn {
    width: 100%;
    height: fit-content;
    padding: 8px 0;
    color:rgb(245, 245, 245);
    background-color: rgb(234, 11, 4);
    border-radius: 4px;
    font-size: 14px;
    font-weight: 500;
    transition: all;
    transition-duration: .4s;
}
.btn:hover {
    background-color: rgb(255, 47, 47);
}

